html, body {
  height: 100%;
  font-family: 'Raleway', 'Source Sans Pro', sans-serif;
}

#app-mount {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#app {
  -ms-flex-item-align: center;
      align-self: center;
  height: 100%;
  width: 100%;
}

#board {
  position: relative;
  height: 100%;
  width: 50%;
  margin: 0 auto; 
  display: -ms-flexbox; 
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

#start {
  position: absolute;
  text-align: center;
  margin: 0 auto;
  width: 50%;
  z-index: 23;
}

#winner-text {
  position: absolute;
  text-align: center;
  margin: 0 auto;
  width: 50%;
  z-index: 42;
}

.no-win {
  display: none;
}

table {
  border-collapse: collapse;
  position: absolute;
  -webkit-transform: rotate(270deg);
      -ms-transform: rotate(270deg);
          transform: rotate(270deg);
}

td {
  border: None;
  height: 100px;
  width: 100px;
  padding: 0;
}

.bottom {
  border-left: 2px solid #14AFFA;
}

.cell {
  height: 70%;
  width: 70%;
  margin: auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.played {
  border-style: none;
  border-width: 2px;
  border-radius: 50%;
}

.player1 {
  background-color: #FD0531;
}

.player2 {
  background-color: #FFD805;
}

.started {
  display: none;
}



